import SignIn from "./signIn-model.js";
const signin = new SignIn()
export default {
	data() {
		return {
			userInfo: {},
			continuousDays: 0,
			signinStatus: '立即签到',
			signinClass: 'signin',
			selShowData: 0,
			reward: []
		};
	},
	components: {

	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			// 签到信息查询
			that.getUserSignin(() => {
				callBack && callBack();
			})
		},
		signin() {
			const that = this
			that.runSignin(() => {
				that.getUserSignin(() => {
					callBack && callBack();
				})
			})
		},
		// 签到
		runSignin(callBack) {
			const that = this
			signin.runSignin({}, (res) => {
				if (res.status_code == 'ok') {
					callBack && callBack();
				}
			})
		},
		// 签到信息查询
		getUserSignin(callBack) {
			// if (data.data.data.continuous_day.status === '已签到') {
			// 	this.signinClass = 'already'
			// 	this.signinStatus = '已签到'
			// }
			const that = this
			signin.getUserSignin({}, (res) => {
				if (res.status_code == 'ok') {
					that.signinInfo = res.data
				}
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return signIn.onShareAppMessage(shareData);
	}
}
